import React from 'react';
import { MetaImgWrapper } from './styled';

export interface IImageBorder {
  style?: React.CSSProperties;
  // 图片宽高缺失时，外部容器不能再使用绝对定位, 因为无法确认尺寸
  isStatic?: boolean;
  onRef: React.ForwardedRef<HTMLSpanElement>;
}

const ImageBorder: React.FC<IImageBorder> = (props) => {
  const { style, isStatic, onRef } = props;
  return (
    <MetaImgWrapper
      ref={onRef}
      data-testid="editor-image-real-box"
      style={{
        ...style,
        position: isStatic ? 'static' : undefined,
      }}
    >
      {props.children}
    </MetaImgWrapper>
  );
};

export default ImageBorder;
